export function joinProgram(httpClient: HttpClient): JoinProgramSignature {
  return (programId: string) =>
    universalJoinProgram(
      programId,
      // @ts-ignore
      { httpClient }
    );
}

interface JoinProgramSignature {
  /**
   * Member joins the program. By the type of program, participant in corresponding
   * state will be created.
   * @param - Program to join
   */
  (programId: string): Promise<
    NonNullablePaths<
      JoinProgramResponse,
      | `participant.paymentPendingOptions.paidPlanIds`
      | `participant.joinedOptions.singlePaymentOptions.orderId`
      | `participant.joinedOptions.paidPlanOptions.planIds`
      | `participant.joinedOptions.freeCouponOptions.couponId`
      | `participant.joinedOptions.timeZone`
      | `participant.joinedOptions.startDate`
      | `participant.joinedOptions.joinPath`
      | `participant.completedOptions.startDate`
      | `participant.failedOptions.startDate`
      | `participant.suspendedOptions.startDate`
      | `participant.autoRemovedOptions.startDate`
      | `participant.memberId`
      | `participant.programId`
      | `participant.progress.totalStepsCompleted`
      | `participant.progress.totalStepsAvailable`
      | `participant.progress.completionPercentage`
      | `participant.state`,
      5
    >
  >;
}

