interface BulkInviteToProgramSignature {
  /**
   * Bulk invites participants to program.
   * For each member participant in Invited state is created.
   * @param - Program to which bulk invite participants.
   */
  (programId: string, options?: BulkInviteToProgramOptions): Promise<
    NonNullablePaths<
      BulkInviteToProgramResponse,
      | `results`
      | `results.${number}.itemMetadata.originalIndex`
      | `results.${number}.itemMetadata.success`
      | `results.${number}.itemMetadata.error.code`
      | `results.${number}.itemMetadata.error.description`
      | `results.${number}.item.joinedOptions.timeZone`
      | `results.${number}.item.joinedOptions.startDate`
      | `results.${number}.item.joinedOptions.joinPath`
      | `results.${number}.item.completedOptions.startDate`
      | `results.${number}.item.failedOptions.startDate`
      | `results.${number}.item.suspendedOptions.startDate`
      | `results.${number}.item.autoRemovedOptions.startDate`
      | `results.${number}.item.memberId`
      | `results.${number}.item.programId`
      | `results.${number}.item.progress.totalStepsCompleted`
      | `results.${number}.item.progress.totalStepsAvailable`
      | `results.${number}.item.progress.completionPercentage`
      | `results.${number}.item.state`
      | `bulkActionMetadata.totalSuccesses`
      | `bulkActionMetadata.totalFailures`
      | `bulkActionMetadata.undetailedFailures`,
      6
    >
  >;
}

